body {
    font-family: 'Noto Sans JP', sans-serif;
    color: grey;
	height: 100%;
}

.panels-container {
    margin: 3rem;
}

.panel {
    border-radius: 12px;
    padding: 1rem;
    box-shadow: whitesmoke 0px 4px 16px, whitesmoke 0px 8px 32px;
}

.issue-selection-panel {
    width: 40%;
    float: left;
}

.issue-view-panel {
    width: 55%;
    float: right;
    height: 100%;
    overflow-y: scroll;
    height: 850px;
}

.pagination {
    text-align: center;
}

.pagination button {
    width: 3rem;
    height: 3rem;
    border-radius: 12px;
    background-color: white;
    border: none;
    margin: 1rem;
    box-shadow: whitesmoke 0px 4px 16px, whitesmoke 0px 8px 32px;
    cursor: pointer;
    color: grey;
}


.selected {
    background-color: lightgrey !important;
    opacity: 0.8;
    color: white !important;
    font-weight: bold;;
}

.selection {
    padding: 0.05rem;
    border-radius: 12px;
}

.selection:hover {
    box-shadow: whitesmoke 0px 4px 16px, whitesmoke 0px 8px 32px;
    background-color: whitesmoke;
    cursor: pointer;
}

.search {
    border: none;
    height: 2rem;
    width: 95%;
    margin-bottom: 2rem;
    font-size: 20px;
    color: grey;
    text-align: center;
}


.issue-view-panel .title {
    text-align: center;
    color: black;
    margin: 0rem;
}

.issue-view-panel .created-by {
    text-align: center;
}

.issue-view-panel .body {
    margin-top: 4rem;
    text-align: justify;
    font-weight: normal;
}

.comment {
    border: 2px solid whitesmoke;
    border-radius: 12px;
    padding: 2rem;
    margin: 2rem;
}

.open {
    color: green !important
}

.closed {
    color: red !important
}

.original-author {
    padding-top: 3rem;
    padding-bottom: 3rem;
    box-shadow: lightgrey 0px 4px 16px;
    margin-bottom: 5rem;
}


.user-profile {
    display: flex;
    cursor: pointer;
}

.username {
    margin: 0.5rem;
    margin-left: 1rem;
    color: #0052A1;
}

.avatar {
    border-radius: 100%;
    height: 3rem;
}